import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import { VectorStyles } from "@/core/geom/default.js";
import TLayer from "./BaseLayer";
import { sameCoord, pointForEach } from "@/utils/index.js";
import { getVectorContext } from "ol/render";
import { easeOut } from "ol/easing";
import { Circle as CircleStyle, Stroke, Style } from "ol/style";
import { unByKey } from "ol/Observable";

class TVectorLayer extends TLayer {
  // é—ªçƒçŠ¶æ€
  _flash = false;

  // æ ·å¼
  styles = {};

  constructor(opt) {
    super(opt);
    this.olLayer = this.createLayer(opt);
    this.initStyle();
  }

  initStyle() {
    this.styles = {
      default: VectorStyles,
      red: VectorStyles,
      blue: VectorStyles,
    };
  }

  createLayer(opt) {
    const {
      type = "vector",
      className, // è¦è®¾ç½®ä¸ºå›¾å±‚å…ƒç´ çš„ CSS ç±»åç§°ã€‚
      opacity, // ä¸é€æ˜Žåº¦ (0, 1)ã€‚
      visible, // èƒ½è§åº¦
      zIndex, // å›¾å±‚æ¸²æŸ“çš„ z-indexã€‚åœ¨æ¸²æŸ“æ—¶ï¼Œå›¾å±‚å°†è¢«æŽ’åºï¼Œé¦–å…ˆæ˜¯ Z-indexï¼Œç„¶åŽæ˜¯ä½ç½®ã€‚å½“ æ—¶undefinedï¼ŒzIndexå¯¹äºŽæ·»åŠ åˆ°åœ°å›¾layersé›†åˆä¸­Infinityçš„å›¾å±‚æˆ–ä½¿ç”¨å›¾å±‚çš„setMap() æ–¹æ³•æ—¶ï¼Œå‡å®šaä¸º 0
      minResolution, // æ­¤å›¾å±‚å¯è§çš„æœ€å°åˆ†è¾¨çŽ‡
      maxResolution, // ä½ŽäºŽæ­¤å›¾å±‚å°†å¯è§çš„æœ€å¤§åˆ†è¾¨çŽ‡
      minZoom, // æœ€å°è§†å›¾ç¼©æ”¾çº§åˆ«ï¼ˆç‹¬å ï¼‰ï¼Œé«˜äºŽè¯¥çº§åˆ«å°†æ˜¾ç¤ºæ­¤å›¾å±‚
      maxZoom, // æ­¤å›¾å±‚å¯è§çš„æœ€å¤§è§†å›¾ç¼©æ”¾çº§åˆ«
      properties, // ä»»æ„å¯è§‚å¯Ÿçš„å±žæ€§ã€‚å¯ä»¥é€šè¿‡#get()å’Œè®¿é—®#set()
    } = opt;
    const source = new VectorSource({
      features: [],
    });
    const self = this;
    const vectorLayer = new VectorLayer({
      source,
      style: function (feature) {
        const type = feature.get("_type") ?? "default";
        feature.setStyle(self.styles[type]);
      },
      ...opt,
    });

    return vectorLayer;
  }

  // æ‰¹é‡æ›´æ–°ç‚¹ä½
  updatePoints(points) {
    const source = this.olLayer.getSource();
    let features = { ...source.idIndex_ };
    pointForEach(
      points,
      (point) => {
        const id = this.getPropertyByMapping(point)("id");
        const feature = features[id];
        // å­˜åœ¨è¯¥å¯¹è±¡ï¼Œæ›´æ–°
        if (feature) {
          delete features[id];
          this._updatePoint(feature, point);
          return;
        }
        //ä¸å­˜åœ¨åˆ›å»º
        this.addPoint(point);
      },
      this
    );

    const delFeatrues = Object.values(features);
    for (let i = 0; i < delFeatrues.length; i++) {
      const feature = delFeatrues[i];
      source.removeFeature(feature);
    }
  }

  // æ›´æ–°å•ä¸ªç‚¹ä½æ–¹æ³•
  _updatePoint(feature, val) {
    const newVal = this.getPropertyByMapping(val);
    const type = newVal("type");
    const coord = [newVal("x"), newVal("y")];
    const lastCoord = feature.getCoordinates();
    if (!sameCoord(lastCoord, coord)) {
      feature.setCoordinates(coord);
    }
    feature.set("value", val);
    feature.set("_type", type);
    this._add(feature);
  }

  // æ·»åŠ ç‚¹ä½
  addPoint(val) {
    const source = this.olLayer.getSource();
    const newVal = this.getPropertyByMapping(val);
    const id = newVal("id");
    const lastFeature = source.idIndex_[id];

    // åˆ¤æ–­æ˜¯å¦å­˜åœ¨è¯¥ç‚¹ä½ï¼Œå¦‚æžœå­˜åœ¨åˆ¤æ–­ä½ç½®æ˜¯å¦ç›¸åŒï¼Œä¸ç›¸åŒåˆ™æ›´æ–°ä½ç½®
    if (lastFeature) {
      this._updatePoint(lastFeature, val);
      return;
    }

    // æ ¹æ®å‚æ•°å€¼ï¼ŒèŽ·å–å¯¹åº”featureå¯¹è±¡
    const feature = this.getPointObj(val);
    //feature.setStyle(this.style)
    this._add(feature);
    source.addFeature(feature);
  }

  // æ‰¹é‡æ·»åŠ ç‚¹ä½
  addPoints(points) {
    pointForEach(
      points,
      (point) => {
        this.addPoint(point);
      },
      this
    );
  }

  // èŽ·å–æ‰€æœ‰ç‚¹ä½feature
  getPoints() {
    const source = this.olLayer.getSource();
    return source.getFeatures();
  }

  // æ ¹æ®idèŽ·å–æŸä¸ªç‚¹feature
  getPointById(id) {
    const source = this.olLayer.getSource();
    const feature = source.getFeatureById(id);
    return feature;
  }

  // è®¾ç½®é—ªçƒåŠ¨ç”»
  setFlash() {
    if (this._flash) {
      return;
    }
    this._flash = true;
    const self = this;
    const duration = 3000;
    const vectorLayer = this.olLayer;
    const source = vectorLayer.getSource();
    this.listenerKey = vectorLayer.on("postrender", animate);

    let start = Date.now();
    let timeout = null;
    function animate(event) {
      const frameState = event.frameState;
      const elapsed = frameState.time - start;
      const vectorContext = getVectorContext(event);
      const elapsedRatio = elapsed / duration;
      // radius will be 5 at start and 30 at end.
      let radius = easeOut(elapsedRatio) * 25 + 5;
      let opacity = easeOut(1 - elapsedRatio);
      if (opacity < 0 && !timeout) {
        radius = 5;
        opacity = 1;
        timeout = setTimeout(() => {
          start = Date.now();
          timeout = null;
          setStyle(radius, opacity);
        }, 1000);
      } else {
        setStyle(radius, opacity);
      }
      function setStyle(r, o) {
        const style = new Style({
          image: new CircleStyle({
            radius: r,
            stroke: new Stroke({
              color: "rgba(255, 0, 0, " + o + ")",
              width: 0.25 + o,
            }),
          }),
        });

        vectorContext.setStyle(style);
        const features = source.getFeatures();
        features.forEach((f) => {
          if (!f._visible) {
            return;
          }
          vectorContext.drawGeometry(f.getGeometry());
        });
        self.map.render();
      }
    }
    self.map.render();
  }

  // å…³é—­é—ªçƒ
  closeFlash() {
    this._flash = false;
    unByKey(this.listenerKey);
  }

  //è®¾ç½®å›¾å±‚éšè—
}

TVectorLayer.prototype.name = "vector-layer";

export default TVectorLayer;
