import Map from "ol/Map";
import View from "ol/View";
import TileLayer from "ol/layer/Tile";
import XYZ from "ol/source/XYZ";
import { defaults as defaultControls } from "ol/control";
import { MAP_URL } from "@/constant/index.js";
import {
  TVectorLayer,
  TClusterLayer,
  TrailLayer,
  ClusterControl,
  VectorControl,
  TPolygonLayer,
} from "@/core/layers/index.js";
import { RotateNorthControl } from "@/core/control/index.js";
import { pointForEach } from "@/utils/index.js";
import Draw from "../layers/Draw.js";

// å°è£…å¥½çš„å¯¹åº”å›¾å±‚
const _controlLayer = {
  vector: VectorControl,
  cluster: ClusterControl,
};

// å°è£…å¥½çš„å¯¹åº”å›¾å±‚
const _typeLayer = {
  vector: TVectorLayer,
  cluster: TClusterLayer,
  trail: TrailLayer,
  polygon: TPolygonLayer,
};

// åœ°å›¾åˆå§‹åŒ–æ–¹æ³•
function _init(config = {}) {
  this._typeLayer = _typeLayer;
  this._controlLayer = _controlLayer;
  const {
    id = "map",
    center = [116.3, 39.9], // ä¸­å¿ƒç‚¹
    zoom = 10, // åˆå§‹åŒ–åœ°å›¾å¯è§†çº§åˆ«
    minZoom = 8, // åœ°å›¾å¯è§†æœ€å°çº§åˆ«
    maxZoom = 17, // åœ°å›¾å¯è§†æœ€å¤§çº§åˆ«
    extent = [70, 0, 140, 60], // åœ°å›¾å¯è§†èŒƒå›´,
    onFinish = () => {},
    url = MAP_URL["gaode"],
  } = config;

  this.id = id;
  this._mapconfig = {
    id,
    center,
    zoom,
    minZoom,
    maxZoom,
    extent,
    url,
  };
  this._url = new XYZ({
    url: MAP_URL["gaode"],
  });

  this.map = new Map({
    controls: defaultControls().extend([new RotateNorthControl()]),
    target: id,
    layers: [
      new TileLayer({
        source: this._url,
      }),
    ],
  });

  this.setConfig();

  this.map._tlayers = [];

  // æ·»åŠ featureç‚¹å‡»äº‹ä»¶
  const _click = (event) => {
    const feature = this.map.forEachFeatureAtPixel(event.pixel, (feature) => {
      //feature.dispatchEvent({ type: "singleclick", event: event });
      return feature;
    });
    feature && feature.dispatchEvent({ type: "singleclick", event: event });
  };
  this.map.on("click", _click);
}

function setConfig(config) {
  this._mapconfig = { ...this._mapconfig, ...config };
  const { center, zoom, minZoom, maxZoom, extent, url } = this._mapconfig;
  this.setExtent(extent);
  this.setCenter(center);
  this.setZoom(zoom);
  this.setMinZoom(minZoom);
  this.setMaxZoom(maxZoom);
  this.setUrl(url);
}

function addLayer(opt = {}) {
  const {
    type = "cluster",
    className, // è¦è®¾ç½®ä¸ºå›¾å±‚å…ƒç´ çš„ CSS ç±»åç§°ã€‚
    opacity, // ä¸é€æ˜Žåº¦ (0, 1)ã€‚
    visible, // èƒ½è§åº¦
    zIndex, // å›¾å±‚æ¸²æŸ“çš„ z-indexã€‚åœ¨æ¸²æŸ“æ—¶ï¼Œå›¾å±‚å°†è¢«æŽ’åºï¼Œé¦–å…ˆæ˜¯ Z-indexï¼Œç„¶åŽæ˜¯ä½ç½®ã€‚å½“ æ—¶undefinedï¼ŒzIndexå¯¹äºŽæ·»åŠ åˆ°åœ°å›¾layersé›†åˆä¸­Infinityçš„å›¾å±‚æˆ–ä½¿ç”¨å›¾å±‚çš„setMap() æ–¹æ³•æ—¶ï¼Œå‡å®šaä¸º 0
    minResolution, // æ­¤å›¾å±‚å¯è§çš„æœ€å°åˆ†è¾¨çŽ‡
    maxResolution, // ä½ŽäºŽæ­¤å›¾å±‚å°†å¯è§çš„æœ€å¤§åˆ†è¾¨çŽ‡
    minZoom, // æœ€å°è§†å›¾ç¼©æ”¾çº§åˆ«ï¼ˆç‹¬å ï¼‰ï¼Œé«˜äºŽè¯¥çº§åˆ«å°†æ˜¾ç¤ºæ­¤å›¾å±‚
    maxZoom, // æ­¤å›¾å±‚å¯è§çš„æœ€å¤§è§†å›¾ç¼©æ”¾çº§åˆ«
    properties, // ä»»æ„å¯è§‚å¯Ÿçš„å±žæ€§ã€‚å¯ä»¥é€šè¿‡#get()å’Œè®¿é—®#set()
  } = opt;
 
  const layer = new this._typeLayer[type](opt);

  layer.bind(this.map);
  // æ”¶é›†å›¾å±‚å¯¹è±¡
  this.map._tlayers.push(layer);

  this.map.addLayer(layer.olLayer);

  return layer;
}

function addControlLayer(opt = {}) {
  const { type = "vector" } = opt;
  const layer = new this._controlLayer[type](opt);
  layer.bind(this.map);
  // æ”¶é›†å›¾å±‚å¯¹è±¡
  this.map._tlayers.push(layer);
  return layer;
}

function clearMap() {
  const tlayers = this.map._tlayers;
  tlayers.forEach((tlayer) => tlayer.destroy());
  // this.map._tlayers = [];
}

function addDraw(config) {
  const draw = new Draw(config);
  this.map._tlayers.push(draw);
  draw.bind(this.map);
  draw.reflash();
  return draw;
}

////////mapé…ç½®è®¾ç½®
function setCenter(center) {
  this.map.getView().setCenter(center);
}
function setZoom(zoom) {
  this.map.getView().setZoom(zoom);
}
function setMaxZoom(zoom) {
  this.map.getView().setMaxZoom(zoom);
}
function setMinZoom(zoom) {
  this.map.getView().setMinZoom(zoom);
}
function setExtent(extent) {
  const oldView = this.map.getView();
  const center = oldView.getCenter();
  const zoom = oldView.getZoom();
  const minZoom = oldView.getMinZoom();
  const maxZoom = oldView.getMaxZoom();
  const view = new View({
    center,
    zoom,
    extent,
    minZoom,
    maxZoom,
    projection: "EPSG:4326",
  });
  this.map.setView(view);
}

function setUrl(url) {
  this._url.setUrl(url);
}

function getLayerByName(layername) {
  return this.map._tlayers.find((layer) => layer.className === layername);
}

export {
  _init,
  setConfig,
  addLayer,
  addControlLayer,
  clearMap,
  addDraw,
  setCenter,
  setZoom,
  setMaxZoom,
  setMinZoom,
  setExtent,
  setUrl,
  getLayerByName,
};
