import TObject from "@/utils/Object.js"
export default class BaseControl extends TObject {
    // å›¾å±‚
    _layers = {};
    // æ ·å¼
    _styles = {};
    // åœ°å›¾å¯¹è±¡
    map = null;
    // å›¾å±‚ç±»åž‹åç§°
    className = ""

    // åˆ›å»ºå›¾å±‚çš„å¯¹è±¡
    Layer = null;

    constructor(opt = {}) {
        super(opt);
        this.opt = opt;
    }
    // TODO æ ·å¼è®¾è®¡
    setStyles(styles) {

    }

    bind(map) {
        this.map = map;
    }

    destroy() {
        Object.values(this._layers).forEach(layer => {
            layer.destroy();
        });
        this._layers = {};
    }

    addPoints(points) {
        const types = this._dealPoints(points)

        // åˆ†å¥½çš„ç±»è¿›è¡Œåˆ›å»ºå›¾å±‚
        Object.keys(types).forEach(type => {
            const layer = this.getLayerByType(type);
            const pts = types[type]
            layer.addPoints(pts);
        })
    }

    updatePoints(points) {
        const types = this._dealPoints(points);
        // å°†ä»¥å­˜åœ¨çš„å›¾å±‚ç±»åž‹ï¼Œå’Œæœ€æ–°ç‚¹ä½çš„å›¾å±‚ç±»åž‹åŽ»é‡
        const typeNames = [...new Set(Object.keys(this._layers).concat(Object.keys(types)))]
        // åˆ†å¥½çš„ç±»è¿›è¡Œåˆ›å»ºå›¾å±‚
        typeNames.forEach(type => {
            const layer = this.getLayerByType(type);
            const pts = types[type] ?? [];
            layer.updatePoints(pts);
        });
    }

    // èŽ·å–å·²æœ‰çš„å›¾å±‚ï¼Œå¦‚æžœæ²¡æœ‰è¯¥å›¾å±‚åˆ™ç›´æŽ¥åˆ›å»º
    getLayerByType(type) {
        if (this._layers[type]) {
            return this._layers[type]
        }
        const layer = new this.Layer({ className: this.className + "-" + type }, this.mapping);
        layer.bind(this.map);
        this.map.addLayer(layer.olLayer);
        this._layers[type] = layer;
        return layer
    }

    setVisibleByType(type, key) {
        const layer = this.getLayerByType(type)
        layer.setVisible(key)
    }

    setZIndexByType(type, index) {
        const layer = this.getLayerByType(type)
        layer.setZIndex(index);
    }

    _dealPoints(points) {
        const types = {};
        // ç‚¹ä½æ•°æ®æŒ‰ç…§ç±»åž‹åˆ†ç±»
        points.forEach(point => {
            const type = point[this.mapping.type];
            !types[type] && (types[type] = []);
            types[type].push(point);
        });
        return types
    }
}